body {
  font-family: Arial;
}

.pagination_list {
  height: 360px;
}

.button {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 8px 16px;
  transition: background-color 0.3s;
}

.button:hover:not(.button_active) {
  background-color: #ddd;
}

.button_active {
  background-color: #4caf50;
  color: white;
}

.pagination_select {
  width: 61px;
  height: 33px;
  margin-left: 10px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
}
